import React from 'react'
import { makeStyles } from '@mui/styles'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faDiscord, faTwitter } from '@fortawesome/free-brands-svg-icons'
import { Grid, IconButton } from '@mui/material'

const useStyles = makeStyles({
  background: {
    background: '#191826',
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
    position: 'fixed',
  },
  frame: {
    flexGrow: 1,
    width: '100%',
    color: 'white',
    padding: '20px',
    boxSizing: 'border-box',
    // position: 'fixed',
    maxWidth: '1280px',
    // margin: '0px auto',
  },
  logo: {
    width: '130px',
  },
  iconWrap: {
    marginLeft: '20px',
  },
  icon: {
    fontSize: '20px',
    color: 'white',
  },
})

const linkList = {
  twitter: 'https://twitter.com/MIMON_OFFICIAL',
  discord: '',
}

function discordAlert() {
  alert('Coming Soon')
}

const Header = () => {
  const classes = useStyles()

  return (
    <>
      <div className={classes.background}>
        <div className={classes.frame}>
          <Grid container>
            <Grid container justifyContent="center">
              <Grid
                container
                item
                lg={12}
                md={12}
                justifyContent="space-between"
              >
                <Grid item container xs={6}>
                  <img
                    className={classes.logo}
                    src="/assets/header-logo.png"
                    alt="main-logo"
                  />
                </Grid>
                <Grid
                  item
                  xs={6}
                  container
                  direction="row"
                  alignItems="center"
                  justifyContent="flex-end"
                >
                  <div className={classes.iconWrap}>
                    <IconButton
                      size="small"
                      className={classes.iconWrap}
                      aria-label="twitter"
                      target="_blank"
                      href={linkList.twitter}
                    >
                      <FontAwesomeIcon
                        icon={faTwitter}
                        className={classes.icon}
                      />
                    </IconButton>
                  </div>
                  <div className={classes.iconWrap}>
                    <IconButton
                      size="small"
                      className={classes.iconWrap}
                      aria-label="discord"
                      target="_blank"
                      onClick={discordAlert}
                      // href={linkList.discord}
                    >
                      <FontAwesomeIcon
                        icon={faDiscord}
                        className={classes.icon}
                      />
                    </IconButton>
                  </div>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </div>
      </div>
    </>
  )
}

export default Header
