import React from 'react'
import { makeStyles } from '@mui/styles'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faDiscord, faTwitter } from '@fortawesome/free-brands-svg-icons'
import { Grid, IconButton } from '@mui/material'

const useStyles = makeStyles({
  background: {
    background: '#ffffff',
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
  },
  frame: {
    flexGrow: 1,
    width: '100%',
    color: '#EB8F9B',
    padding: '50px 20px',
    boxSizing: 'border-box',
    maxWidth: '1280px',
  },
  logo: {
    width: '130px',
    marginBottom: '30px',
  },
  iconWrap: {
    // marginLeft: '20px',
    marginBottom: '30px',
  },
  icon: {
    fontSize: '20px',
    color: '#EB8F9B',
  },
  footerItem: {
    marginBottom: '20px',
  },
  footerText: {
    fontSize: '0.8rem',
    color: 'gray',
    fontFamily: `'Source Code Pro', 'monospace'`,
  },
  space: {
    marginBottom: '50px',
  },
})

const linkList = {
  twitter: 'https://twitter.com/MIMON_OFFICIAL',
  discord: '',
}

function discordAlert() {
  alert('Coming Soon')
}

const Footer = () => {
  const classes = useStyles()

  return (
    <>
      <div className={classes.background}>
        <div className={classes.frame}>
          <Grid container>
            <Grid container justifyContent="center">
              <Grid
                container
                item
                lg={12}
                md={12}
                justifyContent="space-between"
              >
                <Grid
                  item
                  container
                  lg={2}
                  md={2}
                  xs={12}
                  alignItems="center"
                  justifyContent="center"
                >
                  <img
                    className={classes.logo}
                    src="/assets/footer-logo.png"
                    alt="footer-logo"
                  />
                </Grid>
                <Grid
                  item
                  lg={5}
                  md={5}
                  xs={8}
                  container
                  direction="row"
                  alignItems="center"
                  justifyContent="flex-end"
                >
                  <div className={classes.iconWrap}>
                    <IconButton
                      size="large"
                      className={classes.iconWrap}
                      aria-label="twitter"
                      target="_blank"
                      href={linkList.twitter}
                    >
                      <FontAwesomeIcon
                        icon={faTwitter}
                        className={classes.icon}
                      />
                    </IconButton>
                  </div>
                  <div className={classes.iconWrap}>
                    <IconButton
                      size="large"
                      className={classes.iconWrap}
                      aria-label="discord"
                      target="_blank"
                      onClick={discordAlert}
                      // href={linkList.discord}
                    >
                      <FontAwesomeIcon
                        icon={faDiscord}
                        className={classes.icon}
                      />
                    </IconButton>
                  </div>
                </Grid>
                <Grid
                  item
                  lg={12}
                  md={12}
                  xs={12}
                  container
                  direction="row"
                  alignItems="center"
                  justifyContent="flex-end"
                >
                  <div className={classes.footerText}>
                    ⓒ2021 DE3SFUL LABS, All rights reserved.
                  </div>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </div>
      </div>
    </>
  )
}

export default Footer
