import React from 'react'
import { makeStyles } from '@mui/styles'
import { Grid, Typography } from '@mui/material'

const useStyles = makeStyles({
  background: {
    background: '#ffffff',
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
  },
  frame: {
    flexGrow: 1,
    width: '100%',
    height: '100%',
    color: 'black',
    padding: '50px 10px',
    boxSizing: 'border-box',
    maxWidth: '1280px',
  },
  content: {},
  title: {
    display: 'block',
  },
  bannerImg: {
    width: '80%',
  },
  titleFrame: {
    '@media (max-width: 599px)': {
      display: 'none',
    },
  },
  middleTitle: {
    color: '#604687',
  },
  mobileTitle: {
    display: 'none',
    '@media (max-width: 599px)': {
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      fontSize: '2.3rem',
    },
  },
  mobileText: {
    '@media (max-width: 599px)': {
      display: 'flex',
      justifyContent: 'center',
      padding: '10px',
      boxSizing: 'border-box',
    },
  },
  mobileMiddleText: {
    '@media (max-width: 599px)': {
      display: 'flex',
      justifyContent: 'center',
      fontSize: '1.8rem',
      color: '#604687',
    },
  },
  mobileParagraph: {
    fontFamily: `'Source Code Pro', 'monospace'`,
    '@media (max-width: 599px)': {
      padding: '30px',
      fontSize: '1.5rem',
      lineHeight: '40px',
    },
    fontSize: '1.3rem',
    lineHeight: '30px',
  },
})

const Explain = () => {
  const classes = useStyles()

  return (
    <>
      <div className={classes.background}>
        <div className={classes.frame}>
          <div className={classes.mobileTitle}>
            <div className={classes.mobileText}>
              <b>What is mimon?</b>
            </div>
            <div className={classes.mobileMiddleText}>
              <b>Beautiful Dream</b>
            </div>
            <div className={classes.mobileMiddleText}>
              <b>: MIMON (美夢)</b>
            </div>
          </div>
          <Grid container className={classes.content}>
            <Grid container justifyContent="center">
              <Grid container item lg={12} md={12}>
                <Grid
                  item
                  container
                  xlg={5}
                  md={5}
                  sm={5}
                  xs={12}
                  justifyContent="center"
                >
                  <img
                    src="/assets/explain-image.png"
                    alt="explain-img"
                    className={classes.bannerImg}
                  />
                </Grid>
                <Grid
                  item
                  lg={7}
                  md={7}
                  sm={7}
                  xs={12}
                  container
                  direction="column"
                  alignItems="flex-start"
                  justifyContent="space-around"
                >
                  <div className={classes.titleFrame}>
                    <Typography variant="h3">
                      <b>What is mimon?</b>
                    </Typography>
                    <Typography variant="h6" className={classes.middleTitle}>
                      <b>Beautiful Dream : MIMON (美夢)</b>
                    </Typography>
                  </div>
                  <div className={classes.mobileParagraph}>
                    Mimon is a randomly generated collection of 10,000 NFTs
                    based on the Ethereum blockchain. Each Mimon created through
                    programming with the combination of more than 151 attributes
                    is a manifestation of latent desires in our dreams, and all
                    Mimons are very special. face to face the latent desires of
                    your subconscious mind!
                  </div>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </div>
      </div>
    </>
  )
}

export default Explain
