import React from 'react'
import { makeStyles } from '@mui/styles'
import { Grid } from '@mui/material'

const useStyles = makeStyles({
  background: {
    background: '#FFFFFF',
    width: '100%',
    justifyContent: 'center',
    fontFamily: `'Inter', 'sans-serif'`,
  },
  frame: {
    flexGrow: 1,
    color: 'white',
    padding: '20px 20px 50px 20px',
    boxSizing: 'border-box',
    maxWidth: '1280px',
  },
  content: {
    marginTop: '70px',
  },
  textWrap: {
    display: 'flex',
    width: '100%',
    marginTop: '50px',
    '@media (max-width: 599px)': {
      justifyContent: 'center',
      marginTop: '10px',
      marginBottom: '10px',
    },
  },
  title: {
    display: 'block',
    fontSize: '2rem',
    marginBottom: '10px',
    letterSpacing: '5px',
  },
  bannerImg: {
    width: '100%',
  },
  btn: {
    display: 'flex',
    borderRadius: '50px',
    background: '#ED8D8D',
    width: '170px',
    height: '30px',
    justifyContent: 'center',
    alignItems: 'center',
    padding: '5px 15px',
    marginBottom: '20px',
  },
})

const Banner = () => {
  const classes = useStyles()
  return (
    <>
      <div className={classes.background}>
        <div className={classes.frame}>
          <Grid container className={classes.content}>
            <Grid container item lg={12} md={12} justifyContent="space-between">
              <Grid
                item
                container
                lg={6}
                md={6}
                sm={6}
                xs={12}
                direction="column"
                justifyContent="center"
              >
                <Grid item container lg={6} md={6} sm={6} xs={12}>
                  <Grid item container lg={12} md={12} sm={12} xs={12}>
                    <div>
                      <b className={classes.title}>Beautiful Dream</b>
                      <b className={classes.title}>MIMON (美夢)</b>
                    </div>
                  </Grid>
                  <Grid item container lg={12} md={12} sm={12} xs={12}>
                    <div className={classes.textWrap}>
                      <div className={classes.btn}>
                        <b>COMING SOON</b>
                      </div>
                    </div>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item container lg={5} md={5} sm={6} xs={12}>
                <img
                  src="/assets/banner-image.png"
                  alt="banner-img"
                  className={classes.bannerImg}
                />
              </Grid>
            </Grid>
          </Grid>
        </div>
      </div>
    </>
  )
}

export default Banner
